import React, { useState, useEffect } from "react";
import Calendar from "react-calendar";
import Modal from "react-modal";
import "./ImageCalendar.css";
import config from ".//../config"; // Update the import path based on your project structure

Modal.setAppElement("#root"); // Set the app root element for accessibility

function ImageCalendar() {
  // Create state variables for caching
  const [cachedRegions, setCachedRegions] = useState([]);
  const [imageCache, setImageCache] = useState({});

  const [selectedDate, setSelectedDate] = useState(null);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [regionEventData, setRegionEventData] = useState([]);
  const [eventData, setEventData] = useState([]);
  const [showMoreInfoModal, setShowMoreInfoModal] = useState(false);
  const [selectedImage, setSelectedImage] = useState(null);
  const [selectedRegion, setSelectedRegion] = useState(null);
  const [regions, setRegions] = useState([]);
  const [distinctNeighborhoods, setDistinctNeighborhoods] = useState([]);
  const [distinctEventVenues, setDistinctEventVenues] = useState([]);
  const [distinctTags, setDistinctTags] = useState([]);
  const [selectedTags, setSelectedTags] = useState([]);
  // Define state variables to store selected neighborhood and event venue
  const [selectedNeighborhood, setSelectedNeighborhood] = useState("");
  const [selectedEventVenue, setSelectedEventVenue] = useState("");
  // const [includeReported, setIncludeReported] = useState(false);
  // const [includePendingApproval, setIncludePendingApproval] = useState(false);

  useEffect(() => {
    // Fetch regions and cache the result
    async function fetchRegions() {
      if (cachedRegions.length === 0) {
        try {
          const response = await fetch(config.regionsTsvUrl);
          const textData = await response.text();
          const regionsArray = textData
            .split("\n")
            .map((region) => region.trim());
          setCachedRegions(regionsArray);
          setRegions(regionsArray);
        } catch (error) {
          console.error("Error fetching regions:", error);
        }
      } else {
        // Use cached images for the selected region
        setRegions(cachedRegions);
      }
    }

    if (!cachedRegions.length) {
      // Only fetch regions if they are not cached
      fetchRegions();
    }
  }, [cachedRegions]);

  useEffect(() => {
    if (selectedRegion) {
      async function fetchEventData() {
        if (!imageCache[selectedRegion]) {
          try {
            const eventResponse = await fetch(config.submissionsTsvUrl); // Update the URL to point to your TSV file
            const eventTextData = await eventResponse.text();
            const lines = eventTextData.split("\n");
            const parsedEventData = lines
              .map((line) => {
                const [
                  id,
                  submitTimestamp,
                  imageUrl,
                  eventDates,
                  eventWebsite,
                  region,
                  neighborhood,
                  eventVenue,
                  eventTitle,
                  eventNotes,
                  startTime,
                  endTime,
                  tags,
                  reported,
                  approved,
                ] = line.split("\t"); // Split by tab character (\t) for TSV

                const datesArray = eventDates
                  .split(",")
                  .map((date) => date.trim()); // Split and clean up the combined dates

                const tagList = tags.split(",").map((tag) => tag.trim()); // Parse tags

                return {
                  id,
                  submitTimestamp,
                  imageUrl,
                  eventDates: datesArray,
                  eventWebsite,
                  region,
                  neighborhood,
                  eventVenue,
                  eventTitle,
                  eventNotes,
                  startTime,
                  endTime,
                  tags: tagList, // Store tags as an array
                  reported,
                  approved,
                };
              })
              .filter((image) => image.region === selectedRegion); // Filter images by selected region
            // Store the fetched images in the cache
            setImageCache({
              ...imageCache,
              [selectedRegion]: parsedEventData,
            });

            // Create dropdown filters for distinct neighborhood and eventVenue
            const distinctNeighborhoods = Array.from(
              new Set(parsedEventData.map((event) => event.neighborhood))
            ).sort();
            setDistinctNeighborhoods(distinctNeighborhoods);
            const distinctEventVenues = Array.from(
              new Set(parsedEventData.map((event) => event.eventVenue))
            ).sort();
            setDistinctEventVenues(distinctEventVenues);

            // setEventData(parsedEventData);
            setRegionEventData(parsedEventData);
            // Get distinct tags from all events
            const allTags = parsedEventData.reduce((tags, event) => {
              event.tags.forEach((tag) => tags.add(tag));
              return tags;
            }, new Set());

            const sortedTags = Array.from(allTags).sort(); // Sort the tags alphabetically
            setDistinctTags(sortedTags);
          } catch (error) {
            console.error("Error fetching event data:", error);
          }
        } else {
          // Use cached images for the selected region
          setRegionEventData(imageCache[selectedRegion]);
        }
      }
      fetchEventData();
    }
  }, [selectedRegion, imageCache]);

  // Filter based on tags
  useEffect(() => {
    const filteredEvents = regionEventData.filter((event) => {
      // Check if the event matches the selected tags (if any)
      const tagMatch =
        selectedTags.length === 0 ||
        selectedTags.every((tag) => event.tags.includes(tag));

      // Check if the event matches the selected neighborhood (if any)
      const neighborhoodMatch =
        !selectedNeighborhood || event.neighborhood === selectedNeighborhood;

      // Check if the event matches the selected event venue (if any)
      const eventVenueMatch =
        !selectedEventVenue || event.eventVenue === selectedEventVenue;

      // Return true if the event matches all selected filters, otherwise false
      return tagMatch && neighborhoodMatch && eventVenueMatch;
    });

    setEventData(filteredEvents);

    // Update neighborhood filter options based on filtered events
    if (!selectedNeighborhood && !selectedEventVenue) {
      // Only update if neighborhood is not selected
      const neighborhoods = Array.from(
        new Set(filteredEvents.map((event) => event.neighborhood))
      ).sort();
      setDistinctNeighborhoods(["", ...neighborhoods]);
    }

    // Update event venue filter options based on filtered events
    if (!selectedEventVenue) {
      // Only update if event venue is not selected
      const eventVenues = Array.from(
        new Set(filteredEvents.map((event) => event.eventVenue))
      ).sort();
      setDistinctEventVenues(["", ...eventVenues]);
    }
    // Update available tags based on filtered events
    const allTags = filteredEvents.reduce((tags, event) => {
      event.tags.forEach((tag) => tags.add(tag));
      return tags;
    }, new Set());
    const sortedTags = Array.from(allTags).sort();
    setDistinctTags(sortedTags);
  }, [selectedTags, selectedNeighborhood, selectedEventVenue, regionEventData]);

  // Function to handle neighborhood filter selection
  const handleNeighborhoodFilter = (neighborhood) => {
    setSelectedNeighborhood(neighborhood);
    // clear the Venue filter if a neighborhood is selected
    setSelectedEventVenue(null);
  };

  // Function to handle event venue filter selection
  const handleEventVenueFilter = (venue) => {
    setSelectedEventVenue(venue);
  };

  // Handle tag click to add or remove from selected tags
  const handleTagClick = (tag) => {
    if (selectedTags.includes(tag)) {
      setSelectedTags(selectedTags.filter((t) => t !== tag));
    } else {
      setSelectedTags([...selectedTags, tag]);
    }
  };

  const handleDateClick = (date) => {
    setSelectedDate(date);
    setIsModalOpen(true);
  };

  const closeModal = () => {
    setIsModalOpen(false);
    setSelectedDate(null);
  };

  const handleMoreInfoClick = (image) => {
    setSelectedImage(image);
    setShowMoreInfoModal(true);
  };

  const closeMoreInfoModal = () => {
    setSelectedImage(null);
    setShowMoreInfoModal(false);
  };

  const matchingImages = selectedDate
    ? eventData.filter(({ eventDates }) =>
        eventDates.includes(selectedDate.toISOString().split("T")[0])
      )
    : [];

  // Define the specific tags you want to display first
  const level1Tags = ["Food Deals", "Alcohol Deals", "Something To Do"];

  // Define the specific tags you want to display first
  const level2Tags = ["Specific Dates", "Regularly Recurring"];

  // Create a new array that combines the specific tags and the remaining tags
  const combinedTags = [
    ...level1Tags,
    ...level2Tags,
    ...distinctTags.filter(
      (tag) => !level1Tags.includes(tag) && !level2Tags.includes(tag)
    ),
  ];

  const handleReload = () => {
    window.location.reload(); // This will reload the page
  };

  // Function to reset all filters
  const resetFilters = () => {
    setSelectedTags([]);
    setSelectedNeighborhood(null);
    setSelectedEventVenue(null);
  };

  return (
    <div>
      {/* Reload button */}
      <div>
        <button className="common-button" onClick={handleReload}>
          Reload
        </button>
      </div>
      <div>
        <button
          className="common-button"
          onClick={() => {
            if (selectedRegion) {
              const regionWithPlus = selectedRegion.replace(/\s+/g, "+");
              window.open(
                `${config.regionSubmissionFormUrl}${regionWithPlus}`,
                "_blank"
              );
            } else {
              window.open(config.regionSubmissionFormUrl, "_blank");
            }
          }}
        >
          Submit an Event
        </button>
      </div>
      {selectedRegion ? (
        <div>
          {eventData.length > 0 || selectedTags.length > 0 ? (
            <div>
              <button className="common-button" onClick={resetFilters}>
                Reset Filters
              </button>
              <div>
                <select
                  value={selectedNeighborhood || ""}
                  onChange={(e) => handleNeighborhoodFilter(e.target.value)}
                >
                  <option value="">-- Select Neighborhood --</option>
                  {distinctNeighborhoods
                    .filter((neighborhood) => neighborhood.trim() !== "")
                    .map((neighborhood) => (
                      <option key={neighborhood} value={neighborhood}>
                        {neighborhood}
                      </option>
                    ))}
                </select>
              </div>
              <div>
                <select
                  value={selectedEventVenue || ""}
                  onChange={(e) => handleEventVenueFilter(e.target.value)}
                >
                  <option value="">-- Select Event Venue --</option>
                  {distinctEventVenues
                    .filter((venue) => venue.trim() !== "")
                    .map((venue) => (
                      <option key={venue} value={venue}>
                        {venue}
                      </option>
                    ))}
                </select>
              </div>
              <div className="word-cloud">
                Select Tags To Start Filtering<br></br>
                {combinedTags.map((tag, index) => (
                  <React.Fragment key={tag}>
                    {index === level1Tags.length && <br />}{" "}
                    {index === level1Tags.length + level2Tags.length && <br />}{" "}
                    {/* Add a line break after the second tag */}
                    <span
                      key={tag}
                      className={`tag ${
                        selectedTags.includes(tag) ? "selected" : ""
                      }`}
                      onClick={() => handleTagClick(tag)}
                    >
                      {tag}
                    </span>
                  </React.Fragment>
                ))}
              </div>
              <div>
                <Calendar
                  onChange={setSelectedDate}
                  value={selectedDate}
                  calendarType="gregory" // Set calendarType to "gregory" to start on Sunday
                  tileContent={({ date }) => {
                    const dateString = date.toISOString().split("T")[0];

                    const matchingImagesForDate = eventData.filter(
                      ({ eventDates }) => eventDates.includes(dateString)
                    );

                    return (
                      <div
                        className={`tile-content date-box ${
                          selectedDate === date ? "active" : ""
                        }`}
                        onClick={() => handleDateClick(date)}
                      >
                        {matchingImagesForDate.map((image, index) => (
                          <div key={index}>
                            {image.imageUrl ? (
                              <img
                                src={image.imageUrl}
                                alt={`${dateString}`}
                                className="scaled-image"
                              />
                            ) : (
                              <div className="poster">
                                <div className="poster-content">
                                  {`${image.eventTitle}: ${image.eventNotes}`.slice(
                                    0,
                                    500
                                  )}
                                </div>
                              </div>
                            )}
                          </div>
                        ))}
                      </div>
                    );
                  }}
                />
                <Modal
                  isOpen={isModalOpen}
                  onRequestClose={closeModal}
                  className="full-screen-modal"
                  overlayClassName="overlay"
                >
                  <div className="modal-content">
                    {matchingImages.map((image, index) => (
                      <div key={index} className="modal-image-container">
                        {image.imageUrl ? (
                          <img
                            src={image.imageUrl}
                            alt={`${selectedDate.toISOString().split("T")[0]}`}
                            className="modal-image"
                          />
                        ) : (
                          <div className="poster-modal">
                            <div className="poster-content">
                              {`${image.eventTitle}: ${image.eventNotes}`.slice(
                                0,
                                500
                              )}
                            </div>
                          </div>
                        )}

                        <div className="button-container">
                          {image.imageUrl ? (
                            <a
                              href={image.imageUrl}
                              target="_blank"
                              rel="noopener noreferrer"
                              className="common-button"
                            >
                              View Flyer
                            </a>
                          ) : null}
                          {image.eventWebsite ||
                          image.eventTitle ||
                          image.eventNotes ||
                          image.startTime ||
                          image.tags ||
                          image.endTime ? (
                            <button
                              className="common-button"
                              onClick={() => handleMoreInfoClick(image)}
                            >
                              More Info
                            </button>
                          ) : null}
                          <a
                            href={`${
                              config.updateEventFormUrl
                            }${encodeURIComponent(image.id)}`}
                            target="_blank"
                            rel="noopener noreferrer"
                            className="common-button"
                          >
                            Update
                          </a>
                        </div>
                      </div>
                    ))}
                  </div>
                  <button className="close-button" onClick={closeModal}>
                    Close
                  </button>
                </Modal>

                {/* Mini Modal for More Info */}
                {showMoreInfoModal && selectedImage && (
                  <div className="mini-modal">
                    <div className="mini-modal-content">
                      <h2>More Info</h2>
                      {selectedImage.eventWebsite && (
                        <p>
                          Website:{" "}
                          <a
                            href={selectedImage.eventWebsite}
                            target="_blank"
                            rel="noopener noreferrer"
                          >
                            {selectedImage.eventWebsite}
                          </a>
                        </p>
                      )}
                      {selectedImage.eventTitle && (
                        <p>Title: {selectedImage.eventTitle}</p>
                      )}
                      {selectedImage.eventNotes && (
                        <div>
                          <p>Notes:</p>
                          <pre>
                            {selectedImage.eventNotes.replace(/~\|/g, "\n")}
                          </pre>
                        </div>
                      )}
                      {selectedImage.tags && (
                        <p>Tags: {selectedImage.tags.join(", ")}</p>
                      )}
                      {selectedImage.startTime && (
                        <p>Start Time: {selectedImage.startTime}</p>
                      )}
                      {selectedImage.endTime && (
                        <p>End Time: {selectedImage.endTime}</p>
                      )}
                      <button
                        className="close-button"
                        onClick={closeMoreInfoModal}
                      >
                        Close
                      </button>
                    </div>
                  </div>
                )}
              </div>
              <br></br>
              {/* <label>
            <input
              type="checkbox"
              checked={includePendingApproval}
              onChange={() =>
                setIncludePendingApproval(!includePendingApproval)
              }
            />
            Include Events Pending Approval
          </label>
          <br></br>
          <label>
            <input
              type="checkbox"
              checked={includeReported}
              onChange={() => setIncludeReported(!includeReported)}
            />
            Include Reported Events
          </label> */}
            </div>
          ) : (
            <p>Loading Events... Please Wait</p>
          )}
        </div>
      ) : (
        <div>
          {regions.length > 0 ? (
            <div>
              <h2>Select a Region</h2>
              <select onChange={(e) => setSelectedRegion(e.target.value)}>
                <option value="">-- Select a Region --</option>
                {regions.map((region) => (
                  <option key={region} value={region}>
                    {region}
                  </option>
                ))}
              </select>
            </div>
          ) : (
            <p>Loading Regions... Please Wait</p>
          )}
        </div>
      )}
      <div className="footer">
        <p>
          Questions? Want to get involved? Contact{" "}
          <a href="mailto:MixItCity@gmail.com">MixItCity@gmail.com</a>
        </p>
      </div>
    </div>
  );
}

export default ImageCalendar;
